import React, { useState } from 'react'
import { Carousel } from '3d-react-carousal';
import demo from '../assets/Trails.png';
import Magnethik from '../assets/images/rarity/magnethik.png';
import Fire from '../assets/images/rarity/Fire.png';
import Compression from '../assets/images/rarity/compression.png';
import Air from '../assets/images/rarity/Air.png';
import Ethik from '../assets/images/rarity/Ethik.png';
import video1 from '../assets/Diamond_Hands (loop).mp4';
import video2 from '../assets/PunkFracture.mp4';

export default function HeroSlider(props) {
    let slides = [
        <img style={{ width: "500px" }} src={demo} alt="1" />,
        // <video style={{ width: "100%" }} controls autoPlay={true} muted loop>
        //     <source src={video1} type="video/mp4" />
        // </video>,
        <img style={{ width: "500px" }} src={Magnethik} alt="2" />,
        <img style={{ width: "500px" }} src={Fire} alt="3" />,
        // <video style={{ width: "100%" }} controls autoPlay={true} muted loop>
        //     <source src={video2} type="video/mp4" />
        // </video>,
        <img style={{ width: "500px" }} src={Compression} alt="4" />,
        <img style={{ width: "500px" }} src={Air} alt="5" />,
        <img style={{ width: "500px" }} src={Ethik} alt="6" />];

    const callback = function (index) {
        console.log("callback", index);
    }
    return (
        <>
            <Carousel slides={slides} autoplay={true} onSlideChange={callback} />
        </>
    )
}
