import React from 'react'
import { Col, Container, Image, Row } from 'react-bootstrap'
import { Link } from 'react-router-dom'
import GVO from '../assets/$GVO-Token.png'
import GvoBenefit from '../assets/FOUNDER_DAO_.png'
export default function AboutGVO() {
    return (
        <>
            <section className='about_gvo_section'>
                <Container>
                    <Row>
                        <Col lg={8} md={8} sm={12}>
                            <div className='about_gvo_contentDiv'>
                                <div className="about_gvo_content">
                                    <h2>WHAT IS $GVO TOKEN</h2>
                                    <p>$GVO or "Good Vibes Only" is the Utility Token that fuels the Diamond Hands
                                        ecosystem; a combination of minting future collections, exclusive access to
                                        Diamond Hands events, including Metaverse meet-ups, the ability to bid or mint
                                        on small-batch, limited edition 1/1 NFTs and merchandise drops, use $GVO to
                                        build in WEB3 with us or even create your own NFT project within our incubator
                                        program, future partnerships, and more.</p>
                                    <ul>
                                        <li>$GVO=1 $GVO</li>
                                        <li>$GVO will always equal 1 $GVO.</li>
                                        <li>$GVO is primarily a utility and social token and should be viewed as such.</li>
                                    </ul>
                                </div>
                            </div>
                        </Col>
                        <Col lg={4} md={4} sm={12}>
                            <div className="about_gvo_imgDiv">
                                <Image src={GVO} fluid />
                            </div>
                        </Col>
                    </Row>
                    <Row>
                        <Col lg={4} md={4} sm={12}>
                            <div className="about_gvo_benefit_divImg">
                                <Image src={GvoBenefit} fluid />
                            </div>
                        </Col>
                        <Col lg={8} md={8} sm={12}>
                            <div className='about_gvo_benefit_contentDiv_container'>
                                <div className='about_gvo_benefit_contentDiv'>
                                    <h3>BENEFITS OF "GOOD VIBES ONLY"</h3>
                                    <ul>
                                        <li>DeFi (Decentralized Finance)</li>
                                        <li>ETH Validator Node providing growth to Good Vibes DAO</li>
                                        <li>ETH Validator Node Expansion Plans</li>
                                        <li>Staking $LOOKS for Good Vibes DAO</li>
                                        <li>Liquidity Pool created for CryptoPunk 3557 and held in Good Vibes DAO</li>
                                        <li>Membership Access Passes IRL | VR | AR</li>
                                        <li>Access to private Education and Outreach</li>
                                        <li>Access to WEB3 and Blockchain Incubator</li>
                                        <li>Minting future assets</li>
                                        <li>Bidding on small batch NFT's</li>
                                        <li>Hold to Earn</li>
                                    </ul>
                                    <b>Did we mention... Good Vibes? Yeah - you get this too</b> <br />
                                    <Link to="/DH_Rewards">Claim Rewards</Link>
                                </div>
                            </div>
                        </Col>
                    </Row>
                </Container>
            </section>
        </>
    )
}
