import {React, useState, useEffect} from 'react'
import { Link } from 'react-router-dom';
import '../styles/components/_rewards.scss'
function Rewards() {

  const [activeBurn, setActiveBurn] = useState('');
  const [activeStaking, setActiveStaking] = useState('');
  
  useEffect(() => {
    
    if(window.location.pathname === '/stringblock/DH' || window.location.pathname === '/stringblock/DHF' || window.location.pathname === '/stringblock/DHT'){
      setActiveBurn('inner_nav_active')
    } else {
      setActiveBurn('')
    }

    if(window.location.pathname === '/stringblock/DH_Rewards/' || window.location.pathname === '/stringblock/DH_Rewards'){
      setActiveStaking('inner_nav_active')
    } else {
      setActiveStaking('')
    }


  }, []);
  

  return (
    <section className="reward_section">
      {/* <p className="mb-0">My DH Token <span>"Diamond Handing"</span> Rewards</p> */}
      <Link className={`second_nav commit responsive_second_nav ${activeBurn}`} to="/DH">Commit</Link>
      <Link className={`second_nav rewards ${activeStaking}`} to="/DH_Rewards">Rewards</Link>
    </section>
  );
}

export default Rewards;
