// import React from 'react'
import { React, useState, useEffect, useRef } from 'react'
import { Col, Container, Row, Image } from 'react-bootstrap'
import Rewards from './Rewards'
import { Grid } from 'react-loader-spinner'
import GVO from '../assets/$GVO-Token.png'
import Metamask from '../assets/metamask.png'
import { DhState } from '../context/dhContext'
import { BACKEND_URL } from "../env";
import Web3 from "web3";
import { CONTACT_ADDRESS_GVO, CONTACT_ABI_GVO } from '../contract/GVOToken';
import { CONTACT_ADDRESS_BURN_MINT, brun_mint_Abi } from '../contract/mintAndBurnBatch';
import { mint_DHFNFT, CONTACT_ADDRESS_DHFNFT } from '../contract/dhf-nft';



const web3Ndm = new Web3(Web3.givenProvider || 'http://localhost:7545');
const web3_Stake = new Web3(window.ethereum);
export default function DHFRewards() {
    const { data1, setData } = DhState();
    const [reward, setReward] = useState(0);
    const [loader, setloader] = useState(false)
    const [gvoBalance, setGvoBalance] = useState(0)
    const [dhBalance, setDHBalance] = useState(0)
    let loginUserAdd;
    useEffect(async () => {
        window.ethereum.enable().then((address) => {
            loginUserAdd = address[0];
            setData(loginUserAdd)
            const requestOptions1 = {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ metamaskAddress: loginUserAdd })
            };
            fetch(`${BACKEND_URL}addNewuser`, requestOptions1)
                .then(response => response.json())
                .then(metamaskAdd => sessionStorage.setItem("authorization", `Bearer ${metamaskAdd.token}`));
            // sessionStorage.setItem("oneuseforAdd", loginUserAdd);
            console.log("loginUserAddress1loginUserAddress1", loginUserAdd);
            // setloginUserAddress(loginUserAdd)
            // setData(loginUserAdd)
            // console.log("loginUserAddress1loginUserAddress1", loginUserAddress);
        });
        
    
    })
    useEffect(() => {
        var accessToken = sessionStorage.getItem("authorization");
        console.log("accessToken", accessToken);
        const requestOptions1 = {
            method: 'GET',
            headers: { authorization: accessToken, 'Content-Type': 'application/json' },
        };
        fetch(`${BACKEND_URL}GVOreward`, requestOptions1)
            .then(response => response.json())
            .then(totalGvoToken =>
                // console.log("totalGvoToken", totalGvoToken),
                setReward(totalGvoToken.data)
            );
    }, []);

    useEffect(() => {
        setTimeout(() => {
            
            const burnABiWthiCONTRACT = new web3_Stake.eth.Contract(brun_mint_Abi, CONTACT_ADDRESS_BURN_MINT);
            
            console.log("loginUserAdd",loginUserAdd);
            var pausedDate = burnABiWthiCONTRACT.methods.pausedDate().call();
            pausedDate.then(res=>{
                console.log("pausedDate",res);
            })
            var DH_COUNT = burnABiWthiCONTRACT.methods.balanceOf(loginUserAdd).call();
            console.log("DH_COUNT",DH_COUNT);
            DH_COUNT.then(res=>{
                console.log("DHTTOTALMINTED",res);
                setDHBalance(res)
            })
            const http = require("https");

            const options = {
                "method": "POST",
                "hostname": "api-eu1.tatum.io",
                "port": null,
                "path": "/v3/ethereum/smartcontract",
                "headers": {
                    "content-type": "application/json",
                    "x-testnet-type": "ethereum-rinkeby",
                    "x-api-key": "bf846fcb-8fb4-4c74-a0ce-0e9642fc6741"
                }
            };

            const req = http.request(options, function (res) {
                const chunks = [];

                res.on("data", function (chunk) {
                    chunks.push(chunk);
                });

                res.on("end", function () {
                    const body = Buffer.concat(chunks);
                    // console.log(body.toString());
                    console.log("bodybody", JSON.parse(body).data);
                    var dataNFT = JSON.parse(body).data;
                    // setimageDetails(JSON.parse(body).data);
                    console.log("dataNFT", dataNFT);
                    setGvoBalance(dataNFT)
                    // console.log("rewardCollected",rewardCollected);
                    if (dataNFT !== undefined) {
                        console.log("tesing unstake");
                        // setUnStakeSelectArry(dataNFT);
                        // console.log("unStakeSelectArry",unStakeSelectArry);
                        // setstakeDropLoader(true)
                        // setgetNftList(JSON.parse(body).data);
                    }

                });
            });

            req.write(JSON.stringify({
                contractAddress: '0x94ab00945d50f752ba25fc32d757c816c21adbfe',
                methodName: 'balanceOf',
                methodABI: {
                    "inputs":
                    [{"internalType":"address","name":"owner","type":"address"}],"name":"balanceOf","outputs":[{"internalType":"uint256","name":"","type":"uint256"}],"stateMutability":"view","type":"function"
                },
                params: [loginUserAdd],
            }));
            req.end();


        }, 5000)
    }, [])

    function collect() {
        setloader(true)

        const gvoABiWthiCONTRACT = new web3_Stake.eth.Contract(CONTACT_ABI_GVO, CONTACT_ADDRESS_GVO);
        // const web_value = web3Ndm.utils.toWei(rewardCollected, 'ether');
        // console.log("web_value",web_value);
        var inputCollectReward1 = document.getElementById("inputCollectReward").value;
        console.log("inputCollectReward1111", inputCollectReward1);
        var inputCollectReward = inputCollectReward1;
        console.log("inputCollectReward", inputCollectReward);
        var tokens = web3Ndm.utils.toWei(inputCollectReward.toString(), 'ether')
        var bntokens = web3Ndm.utils.toBN(tokens)

        console.log('tokens', tokens);
        console.log('weiValue', bntokens);
        // document.getElementById('collectLoader').style.display = "flex";
        console.log("GVO", inputCollectReward, "CONTACT_ADDRESS_GVO", CONTACT_ADDRESS_GVO);
        if (Math.ceil(inputCollectReward) < 1) {
            inputCollectReward = 1;
        }
        else {
            inputCollectReward = Math.ceil(inputCollectReward)
        }
        console.log("loginUserAdd", loginUserAdd);
        gvoABiWthiCONTRACT.methods.transferGVOToken("0x619a67bc8132D5ce5Fb2D88a9a9912af86d9825b", inputCollectReward)
            .send(
                {
                    from: loginUserAdd,
                    gas: 165000,
                }
            )
            .on('error', function (error) {
                // setDiamondWallet(diamondWallet + parseInt(stakeNumber))
                // console.log(typeof stakeNumber);
                console.log("error", error)
                // let o = stakeNumber.Number()
                // console.log(typeof o);
                // console.log('error');
                document.getElementById('collectLoader').style.display = "none";
                setloader(false)
                window.location.reload()

            }).then(function (info) {
                console.log('success ', info);

                // localStorage.setItem("diamondWalletUnstake", 0);
                setReward(0)
                var accessToken = sessionStorage.getItem("authorization");
                console.log("accessToken", accessToken);
                const requestOptions3 = {
                    method: 'POST',
                    headers: { authorization: accessToken, 'Content-Type': 'application/json' }
                };
                fetch(`${BACKEND_URL}CollectGVOreward`, requestOptions3)
                    .then(response => response.json())
                    .then(collectReward => console.log("collectReward", collectReward))
                    .catch((err) => {
                        console.log("NAdeem err ", err);
                        setloader(false)
                    });
                setloader(false)
                // setDiamondWallet(diamondWallet + stakeNumber)
                // console.log("diamondWallet 186", diamondWallet);
                // setDiamondWallet(diamondWallet + parseInt(stakeNumber.length))
                document.getElementById('collectLoader').style.display = "none";
                window.location.reload()

            });
    }
    // useEffect(() => {
    //     const gvoABiWthiCONTRACT = new web3_Stake.eth.Contract(CONTACT_ABI_GVO, CONTACT_ADDRESS_GVO);
    //     collectGVORef.current = collectGVO;

    // }, [rewardCollected, loginUserAddress])


    // function collect() {
    //     var accessToken = sessionStorage.getItem("authorization");
    //     console.log("accessToken", accessToken);
    //     const requestOptions3 = {
    //         method: 'POST',
    //         headers: { authorization: accessToken, 'Content-Type': 'application/json' }
    //     };
    //     fetch(`${BACKEND_URL}CollectGVOreward`, requestOptions3)
    //         .then(response => response.json())
    //         .then(collectReward => console.log("collectReward", collectReward))
    //         .catch((err) => {
    //             console.log("NAdeem err ", err);
    //         });
    // }

    function addGVO() {
        console.log("addGVO", GVO);
        let address = CONTACT_ADDRESS_GVO;
        let symbol = "GVO";
        let decimals = 0;
        let image = "http://103.15.67.78/stringblock/static/media/$GVO-Token.d79694d8.png";
        const injectedProvider = (window).ethereum;
        // if (provider && account && window && injectedProvider) {
        if (loginUserAdd.toLowerCase() ) {
            injectedProvider.request({
                method: 'wallet_watchAsset',
                params: {
                    type: 'ERC20',
                    options: {
                        address,
                        symbol,
                        decimals,
                        image,
                    },
                },
            });

            return true;
        }
        // }
        // const addERC20Token = async ({
        //     address,
        //     symbol,
        //     decimals,
        //     image,
        //   }) => {
        //     // using window.ethereum as looks like its only supported for metamask
        //     // and didn't manage to make the call with ethersjs
        //     // eslint-disable-next-line @typescript-eslint/no-explicit-any

        //     return false;
        //   };
    }
console.log("reward", reward);

    return (
        <>
            <section className='DHF_Rewards_section'>
                <Rewards />
                <Container>
                    <Row>
                        <Col lg={12}>
                            <div className="DHF_reward_Head">
                                <Image src={GVO} fluid />
                                <button onClick={addGVO}> <Image src={Metamask} fluid /> Add $GVO</button>
                                <p>Click to Automatically Add <b>$GVO</b> Token to your wallet</p>
                            </div>
                        </Col>
                    </Row>
                    <Row id='reward' style={{ paddingTop: "7em" }}>
                        <Col lg={12}>
                            <div className="DHF_rewards_to_collect_div">
                                <div className='DHF_rewards_loaderDiv' style={loader ? { display: "flex" } : { display: "none" }}>
                                    <Grid color="#FFFFFF" height={60} width={60} />
                                    <p>Your Transaction begin process, hold on 10-12 seconds to make the transaction.</p>
                                </div>
                                <h2>Rewards to Collect</h2>
                                <div className='DHF_rewards_inputDiv'>
                                    <input type="text" id='inputCollectReward' value={reward} disabled />
                                    <button className='max_btn'>Max</button>
                                    <button className='collect_btn' onClick={collect}>Collect</button>
                                </div>
                                <ul>
                                    <li>
                                        <div>$GVO Tokens collected to Date</div>
                                        <div>{gvoBalance}</div>
                                    </li>
                                    {/* <li>
                                        <div></div>
                                        <div>0.0</div>
                                    </li> */}
                                    <li>
                                        <div>Diamond Hands</div>
                                        <div>{dhBalance}</div>
                                    </li>
                                </ul>
                                <hr />
                                <div className='DHF_GVO_img_bottom'>
                                    <Image src={GVO} fluid />
                                </div>
                                <ul>
                                    <li>
                                        <div>$GVO Available to Collect</div>
                                        <div>{reward ? reward : 0}</div>
                                    </li>
                                </ul>
                            </div>
                            <button className='DHF_liquidity_pool'>V2 Liquidity Pool</button>
                        </Col>
                    </Row>
                </Container>
            </section>
        </>
    )
}
