import React from 'react'
import { Col, Container, Image, Row } from 'react-bootstrap'
import Logo from '../assets/DH-logo-white.png'

export default function Collectors() {

    return (
        <>
            <section className="collector_section">
                <Container>
                    <Row>
                        <Col lg={12}>
                            <div className='collectors_Head'>
                                <Image src={Logo} fluid />
                                <h2>Humbled by our collectors</h2>
                            </div>
                        </Col>
                        <Col lg={12}>
                            <div className='collector_iframe'>
                                {/* <iframe  src="https://rinkeby.etherscan.io/token/0x0124EC88b666CB62C6C29768dd88B63f0b8bb76B#balances" width={200} height={200} frameborder="0"></iframe> */}
                                <a href="https://etherscan.io/token/0x3290f349a0642229b46b7102d2024b34fe8bd3cc#balances" target="_blank">Collectors</a>
                            </div>
                        </Col>
                    </Row>
                </Container>
            </section>
        </>
    )
}
